import React from 'react';

import { Field, reduxForm } from 'redux-form';

import Button from '@material-ui/core/Button';
import CircularProgress from '@material-ui/core/CircularProgress';

import Card from '../../share/Card/Card';
import renderTextField from '../../share/renderedFields/input';

import { LOGIN_FORM } from '../../constants/reduxForms';

import { required } from '../../validation/validateFields';

import { useTranslation } from 'react-i18next';

let LoginForm = props => {
    const { t } = useTranslation('formElements');
    const { handleSubmit } = props;

    const error = props.loginError;

    const translation = props.translation;

    const errorHandling = value => {
        if (required(value)) {
            props.setError(required(value));
        } else {
            props.setError(null);
        }
    };

    const errArr = error ? error.toLowerCase().split(' ') : '';

    const passwordFieldErrorHandling = () =>
        error &&
        (errArr.includes('fields') ||
            errArr.includes('') ||
            errArr.includes('password') ||
            errArr.includes(''));

    let form = (
        <form onSubmit={handleSubmit}>
            <Field
                name="email"
                className="form-field"
                component={renderTextField}
                label={t('email.label')}
                error={!!error}
                helperText={error}
                onChange={e => {
                    errorHandling(e.target.value);
                }}
            />
            <Field
                name="password"
                className="form-field"
                type="password"
                component={renderTextField}
                label={t('password.label')}
                error={error && passwordFieldErrorHandling()}
                onChange={() => {
                    props.setError(null);
                }}
            />
            <Button
                className="buttons-style under-line"
                type="submit"
                variant="contained"
                color="primary"
            >
                {translation('login.title')}
            </Button>
            <button
                type="button"
                className="auth-link"
                onClick={() => {
                    props.switchAuthMode(false);
                    props.setError(null);
                }}
            >
                {translation('no.account')}
            </button>
        </form>
    );

    if (props.isLoading) {
        form = <CircularProgress />;
    }

    return (
        <section className="auth-form">
            <Card class="auth-card">
                <h2 className="under-line">
                    {translation('login.page.title')}
                </h2>
                {form}
            </Card>
        </section>
    );
};

LoginForm = reduxForm({
    form: LOGIN_FORM
})(LoginForm);

export default LoginForm;
