import React, { useState } from 'react';

import PropTypes from 'prop-types';

import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import Autocomplete from '@material-ui/lab/Autocomplete';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

import '../../share/modals/dialog.scss';

const useStyles = makeStyles(() => ({
    roomField: {
        '&': {
            margin: '0 auto',
            width: 250
        }
    },
}));



const ScheduleDialog = props => {
    const { onClose, itemData, open, rooms, translation } = props;
    const [room, setRoom] = useState('');

    const classes = useStyles();

    const handleClose = () => {
        onClose();
    };

    const chooseClickHandle = () => {
        if (!room) {
            return;
        }
        onClose({ itemData, room });
        setRoom(null);
    };

    const defaultProps = {
        options: rooms,
        getOptionLabel: option => (option ? option.name : '')
    };

    return (
        <Dialog
            disableBackdropClick={true}
            onClose={handleClose}
            aria-labelledby="simple-dialog-title"
            open={open}
        >
            <DialogTitle id="simple-dialog-title">{translation('common:schedule.dialog.title')}</DialogTitle>
            <Autocomplete
                {...defaultProps}
                id="group"
                clearOnEscape
                openOnFocus
                className={classes.roomField}
                onChange={(event, newValue) => {
                    setRoom(newValue);
                }}
                renderInput={params => (
                    <TextField {...params} label={translation('formElements:room.label')} margin="normal" />
                )}
            />
            <div className="buttons-container">
                <Button
                    className="dialog-button"
                    variant="contained"
                    color="primary"
                    onClick={() => chooseClickHandle()}
                >
                    {translation('formElements:choose.button.title')}
                </Button>
                <Button
                    className="dialog-button"
                    variant="contained"
                    onClick={() => onClose()}
                >
                    {translation('formElements:cancel.button.title')}
                </Button>
            </div>
        </Dialog>
    );
};

ScheduleDialog.propTypes = {
    onClose: PropTypes.func.isRequired,
    open: PropTypes.bool.isRequired,
    selectedValue: PropTypes.string.isRequired
};

export default ScheduleDialog;
