import React, {useEffect, useState} from "react";
import { connect } from "react-redux";
import { MdDelete } from "react-icons/md";
import { FaEdit } from 'react-icons/fa';
import {useTranslation} from "react-i18next";

import {showAllGroupsService, removeGroupCardService, handleGroupService, selectGroupService, clearGroupService
} from "../../services/groupService";
import { handleSnackbarCloseService } from '../../services/snackbarService';
import {search} from "../../helper/search";

import SnackbarComponent from '../../share/Snackbar/SnackbarComponent';
import ConfirmDialog from '../../share/modals/dialog';
import AddGroup from '../../components/AddGroupForm/AddGroupForm';
import SearchPanel from "../../share/SearchPanel/SearchPanel";
import './GroupList.scss';
import NotFound from "../../share/NotFound/NotFound";

const GroupList = (props) => {
    useEffect(() => showAllGroupsService(), []);
    const { t } = useTranslation('formElements');

    const [open, setOpen] = useState(false);
    const [groupId, setGroupId] = useState(-1);
    const [term, setTerm] = useState('');

    const { isSnackbarOpen, snackbarType, snackbarMessage } = props;

    const submit = values => {
        handleGroupService(values);
    };

    const handleEdit = groupId => {
        selectGroupService(groupId)
    };

    const handleClickOpen = (groupId) => {
        setGroupId(groupId);
        setOpen(true);
    };

    const handleClose = (groupId) => {
        setOpen(false);
        if (!groupId) {
            return;
        }
        removeGroupCardService(groupId);
    };

    const handleSnackbarClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }

        handleSnackbarCloseService();
    };

    const handleFormReset = () => {
        clearGroupService();
    };

    const visibleItems = search(props.groups, term, ['title']);

    const SearchChange = (term) => {
        setTerm(term);
    };

    return (
        <>
            <ConfirmDialog selectedValue={''} cardId={groupId} whatDelete={'group'} open={open} onClose={handleClose}/>
            <div className="cards-container">
                <aside className="search-list__panel">
                <SearchPanel SearchChange={SearchChange}/>
                <AddGroup className='form' onSubmit={submit} onReset={handleFormReset}/>
                </aside>
                <div className="group-wrapper group-list">
                    {visibleItems.length === 0 && <NotFound name={t('groupy.label')}/>}
                    {visibleItems.map(group => (
                        <section key={group.id} className="group-card">

                            <div className="group__buttons-wrapper">
                                <FaEdit className="group__buttons-edit" title={t('edit.title')}
                                        onClick={() => handleEdit(group.id)}/>
                                <MdDelete className="group__buttons-delete" title={t('delete.title')}
                                          onClick={() => handleClickOpen(group.id)}/>
                            </div>
                            <p className="group-card__description">{t('group.label')+':'}</p>
                            <h1 className="group-card__number">{group.title}</h1>

                        </section>))}
                </div>
            </div>
            <SnackbarComponent
                message={snackbarMessage}
                type={snackbarType}
                isOpen={isSnackbarOpen}
                handleSnackbarClose={handleSnackbarClose}
            />
        </>
    );
};
const mapStateToProps = state => ({
    groups: state.groups.groups,
    isSnackbarOpen: state.snackbar.isSnackbarOpen,
    snackbarType: state.snackbar.snackbarType,
    snackbarMessage: state.snackbar.message
});

export default connect(mapStateToProps, {})(GroupList);
