import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';

import i18next from './i18n';

const dayArray = [
    'MONDAY',
    'TUESDAY',
    'WEDNESDAY',
    'THURSDAY',
    'FRIDAY',
    'SATURDAY',
    'SUNDAY'
];

export const prepareLessonCardCell = card => {
    let inner = '';
    if (card !== undefined && card !== null) {
        inner =
            card.teacherForSite +
            '\n\r' +
            card.subjectForSite +
            '\n\r' +
            card.lessonType;
        if (card.room) {
            inner += '\n\r' + card.room.name;
        }
    }
    return inner;
};

const getWeekNumber = (startScheduleDate, date) => {
    var oneJan = new Date(startScheduleDate);
    var numberOfDays = Math.floor((date - oneJan) / (24 * 60 * 60 * 1000));
    return Math.ceil((date.getDay() + 1 + numberOfDays) / 7);
};
const printWeekNumber = startScheduleDate => {
    var date = new Date();
    var result = getWeekNumber(startScheduleDate, date);
    return result;
};

function isOdd(num) {
    return num % 2;
}

export const renderGroupDayClass = (classDay, isOddWeek, startScheduleDate) => {
    const currentDay = matchDayNumberSysytemToDayName();
    const currentWeekType = isOdd(printWeekNumber(startScheduleDate));

    let res = [];
    for (let [key, value] of Object.entries(classDay.cards)) {
        value.day = key;
        res.push(value);
    }
    return (
        <TableRow>
            <TableCell className=" lesson groupLabelCell">
                {classDay.class.class_name +
                    '\n\r' +
                    classDay.class.startTime +
                    ' - ' +
                    classDay.class.endTime}
            </TableCell>
            {res.map(day => {
                let className = 'lesson ';
                if (currentDay === day.day && currentWeekType === isOddWeek) {
                    className += ' currentDay';
                }
                return (
                    <TableCell align="center" className={className}>
                        {prepareLessonCardCell(day.card, currentDay)}
                    </TableCell>
                );
            })}
        </TableRow>
    );
};

export const renderScheduleGroupHeader = dayArray => (
    <TableHead>
        <TableRow key={0}>
            <TableCell className="groupLabelCell"></TableCell>
            {dayArray.map(day => (
                <TableCell>{i18next.t(`day_of_week_${day}`)}</TableCell>
            ))}
        </TableRow>
    </TableHead>
);

export const renderGroupTable = (classes, isOdd) => (
    <TableContainer>
        <Table aria-label="sticky table">
            {renderScheduleGroupHeader(dayArray)}
            <TableBody>
                {classes.map((classDay, classIndex) => {
                    if (classDay) {
                        return renderGroupDayClass(
                            classDay,
                            isOdd,
                            new Date('04/06/2020')
                        );
                    }
                })}
            </TableBody>
        </Table>
    </TableContainer>
);

export const renderGroupCells = (
    groups,
    isOdd,
    currentWeekType,
    isCurrentDay
) => {
    return groups.map((group, groupIndex) => {
        var colspan = 1;
        var rowspan = 1;
        var classname = 'lesson';

        if (group.card !== null && group.card.skip === 1) {
            return;
        }
        if (group.card !== null && group.card.weekly === 1) {
            rowspan = 2;
            classname += ' weekly';
        }

        if (currentWeekType === isOdd && isCurrentDay) {
            classname += ' currentDay';
        }

        for (let i = groupIndex; i < groups.length; i++) {
            if (
                group &&
                groups[i + 1] &&
                group.card !== null &&
                groups[i + 1].card !== null
            ) {
                if (
                    group.card.teacherForSite &&
                    group.card.teacherForSite ===
                        groups[i + 1].card.teacherForSite &&
                    group.card.subjectForSite ===
                        groups[i + 1].card.subjectForSite &&
                    group.card.room === groups[i + 1].card.room &&
                    group.card.weekly === groups[i + 1].card.weekly &&
                    group.card.skip === groups[i + 1].card.skip
                ) {
                    groups[i + 1].card.skip = 1;
                    colspan++;
                    classname += ' grouped';
                }
            }
        }
        return (
            <TableCell
                align="center"
                colSpan={colspan}
                rowSpan={rowspan}
                className={classname}
            >
                {prepareLessonCardCell(group.card)}
            </TableCell>
        );
    });
};

export const renderScheduleHeader = groups => (
    <TableHead>
        <TableRow key={0}>
            <TableCell className="groupLabelCell" colSpan={2}>
                {i18next.t('groupy.label')}
            </TableCell>
            {groups.map(group => (
                <TableCell align="center">{group}</TableCell>
            ))}
        </TableRow>
    </TableHead>
);

export const renderFirstDayFirstClassFirstCardLine = (
    day_name,
    class_name,
    groups,
    classesCount
) => {
    const currentDay = matchDayNumberSysytemToDayName();
    const currentWeekType = isOdd(printWeekNumber(new Date('04/06/2020')));

    let dayClassName = 'dayNameCell ';
    let classClassName = 'classNameCell ';
    let isCurrentDay = false;
    if (currentDay === day_name) {
        dayClassName += ' currentDay';
        classClassName += ' currentDay';
        isCurrentDay = true;
    }

    if (groups.even.length <= 2 || groups.odd.length <= 2) {
        dayClassName += ' minHeightDouble';
    }
    return (
        <>
            <TableRow key={555 + day_name}>
                <TableCell rowSpan={classesCount * 2} className={dayClassName}>
                    <span className="dayName">
                        <p>{i18next.t(`common:day_of_week_${day_name}`)}</p>
                    </span>
                </TableCell>
                <TableCell className={classClassName} rowSpan={2}>
                    {class_name}
                </TableCell>
                {renderGroupCells(groups.odd, 1, currentWeekType, isCurrentDay)}
            </TableRow>
            <TableRow key={789 + groups.even}>
                {renderGroupCells(
                    groups.even,
                    0,
                    currentWeekType,
                    isCurrentDay
                )}
            </TableRow>
        </>
    );
};

export const renderFirstDayOtherClassFirstCardLine = (class_name, groups) => (
    <>
        <TableRow key={4545 + class_name}>
            <TableCell className="classNameCell" rowSpan={2}>
                {class_name}
            </TableCell>
            {renderGroupCells(groups.odd)}
        </TableRow>
        <TableRow key={789 + groups.even}>
            {renderGroupCells(groups.even)}
        </TableRow>
    </>
);

const prepareForRender = classItem => {
    if (classItem.cards) {
        classItem.cards.odd.map((card, cardIndex) => {
            if (card.group.id === classItem.cards.even[cardIndex].group.id) {
                if (
                    card.card !== null &&
                    classItem.cards.even[cardIndex].card !== null
                ) {
                    if (
                        card.card.teacherForSite ===
                            classItem.cards.even[cardIndex].card
                                .teacherForSite &&
                        card.card.subjectForSite ===
                            classItem.cards.even[cardIndex].card
                                .subjectForSite &&
                        card.card.room.id ===
                            classItem.cards.even[cardIndex].card.room.id
                    ) {
                        classItem.cards.odd[cardIndex].card.weekly = 1;
                        classItem.cards.even[cardIndex].card.skip = 1;
                    }
                }
            }
        });
    }
};

export const renderDay = dayItem => {
    return dayItem.class.map((classItem, classIndex) => {
        var classesCount = dayItem.class.length;
        if (classIndex === 0) {
            prepareForRender(classItem);

            if (classItem.cards) {
                return renderFirstDayFirstClassFirstCardLine(
                    dayItem.day,
                    classItem.class.class_name,
                    classItem.cards,
                    classesCount
                );
            }
        } else {
            prepareForRender(classItem);
            if (classItem.cards) {
                return renderFirstDayOtherClassFirstCardLine(
                    classItem.class.class_name,
                    classItem.cards
                );
            }
        }
    });
};

export const renderScheduleFullHeader = groupList => (
    <TableHead>
        <TableRow key={0}>
            <TableCell colSpan={2}>
                {i18next.t('formElements:group.label')}
            </TableCell>
            {groupList.map(group => (
                <TableCell className="groupLabelCell">{group.title}</TableCell>
            ))}
        </TableRow>
    </TableHead>
);

export const renderFullSchedule = fullSchedule => (
    <TableContainer>
        <Table aria-label="sticky table">
            {renderScheduleFullHeader(fullSchedule.groupList)}
            <TableBody>
                {fullSchedule.resultArray.map((dayItem, dayIndex) => {
                    return renderDay(dayItem, dayIndex);
                })}
            </TableBody>
        </Table>
    </TableContainer>
);

const matchDayNumberSysytemToDayName = () => {
    var now = new Date();
    const dayArray = [
        'SUNDAY',
        'MONDAY',
        'TUESDAY',
        'WEDNESDAY',
        'THRUESDAY',
        'FRIDAY',
        'SATURDAY'
    ];
    return dayArray[now.getDay()];
};
