import React, { useState } from 'react';

import Board from '../Board/Board';

import { days } from '../../constants/schedule/days';

import {
    everyFirstLetterCapital,
    firstStringLetterCapital
} from '../../helper/strings';

import ScheduleItem from '../ScheduleItem/ScheduleItem';

import {
    addItemToScheduleService,
    deleteItemFromScheduleService
} from '../../services/scheduleService';
import {
    getLessonsByGroupService,
    selectGroupIdService
} from '../../services/lessonService';
import { cssClasses } from '../../constants/schedule/cssClasses';
import { makeStyles } from '@material-ui/core/styles';
import ScheduleDialog from '../ScheduleDialog/ScheduleDialog';

const Schedule = props => {
    const { groups, itemGroupId } = props;
    const [open, setOpen] = useState(false);
    const [itemData, setItemData] = useState(null);

    const setNewItemHandle = (item, room, groupId) => {
        getLessonsByGroupService(groupId);
        selectGroupIdService(groupId);
        addItemToScheduleService({ ...item, roomId: room.id });
    };

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = value => {
        setOpen(false);
        if (value) {
            setNewItemHandle(
                value.itemData.item,
                value.room,
                value.itemData.groupId
            );
        }
    };

    const items = props.items;

    const classes = props.classes;

    const boardHeight = 129;
    const margins = 17;

    const t = props.translation;

    const useStyles = makeStyles({
        day: {
            minHeight: classes.length * 2 * (boardHeight + margins)
        }
    });
    const elClasses = useStyles();

    const everyFirstLetterCapitalHandle = str => {
        return everyFirstLetterCapital(str);
    };

    const firstStringLetterCapitalHandle = str => {
        return firstStringLetterCapital(str);
    };

    const deleteItemFromScheduleHandler = (itemId, groupId) => {
        deleteItemFromScheduleService(itemId);
        getLessonsByGroupService(groupId);
        selectGroupIdService(groupId);
    };

    const itemInBoard = (group, lesson, index) => {
        for (let item of items) {
            if (
                `group-${
                    item.lesson.group.id
                }-day-${item.dayOfWeek.toLowerCase()}-class-${
                    item.period.id
                }-week-${item.evenOdd.toLowerCase()}` ===
                `group-${group.id}-day-${lesson.day.name}-class-${lesson.classNumber.id}-week-${lesson.week}`
            ) {
                return (
                    <section
                        key={group.id + index + item.id}
                        className={cssClasses.IN_BOARD_SECTION}
                    >
                        <ScheduleItem
                            inBoard={true}
                            index={`in-day-${lesson.day}-class-${lesson.classNumber.id}-week-${lesson.week}`}
                            classScheduler={classes}
                            class={cssClasses.IN_BOARD_CARD}
                            item={item}
                            deleteItem={deleteItemFromScheduleHandler}
                            fStrLetterCapital={firstStringLetterCapitalHandle}
                            eFLetCapital={everyFirstLetterCapitalHandle}
                            translation={t}
                        />
                    </section>
                );
            }
        }
    };

    const allLessons = [];
    days.forEach((day, index) => {
        classes.forEach(classNumber => {
            for (let i = 0; i < 2; i++) {
                if ((i + 1) % 2 === 0) {
                    allLessons.push({
                        day: { name: day.toLowerCase(), id: index },
                        classNumber,
                        week: 'even'
                    });
                } else {
                    allLessons.push({
                        day: { name: day.toLowerCase(), id: index },
                        classNumber,
                        week: 'odd'
                    });
                }
            }
        });
    });

    return (
        <section className="cards-container">
            <ScheduleDialog
                translation={t}
                selectedValue={''}
                itemData={itemData}
                rooms={props.rooms}
                open={open}
                onClose={handleClose}
            />
            <aside className="day-classes-aside">
                <div className="card empty-card" />
                {days.map((day, index) => (
                    <section className="cards-container" key={day}>
                        <section
                            className={elClasses.day + ' card schedule-day'}
                        >
                            {t(`common:day_of_week_${index + 1}`)}
                        </section>
                        <section className="class-section">
                            {classes.map(classScheduler => (
                                <section
                                    className="card schedule-class"
                                    key={classScheduler.id}
                                >
                                    {t('common:class.schedule')}{' '}
                                    {classScheduler.class_name}
                                </section>
                            ))}
                        </section>
                    </section>
                ))}
            </aside>
            {groups.map(group => (
                <section key={'group-' + group.id}>
                    <div className="group-title card" id={`group-${group.id}`}>
                        {t('common:group.schedule')}
                        {group.title}
                    </div>
                    {allLessons.map((lesson, index) => (
                        <div key={group + '-' + index} className="tooltip-div">
                            <div
                                className="tooltip-board"
                                id={`tooltip-${group.id}-day-${lesson.day.name}-class-${lesson.classNumber.id}-week-${lesson.week}`}
                            >
                                <p>
                                    {t(`common:day_of_week_${+lesson.day.id + 1}`)}
                                </p>
                                <p>
                                    {t('common:class.schedule')}:{' '}
                                    {lesson.classNumber.class_name},{' '}
                                    {t('common:week.title')}:{' '}
                                    {t(`common:week.${lesson.week}.title`)}
                                </p>
                            </div>
                            <Board
                                setModalData={setItemData}
                                openDialog={handleClickOpen}
                                itemGroupId={itemGroupId}
                                id={`group-${group.id}-day-${lesson.day.name}-class-${lesson.classNumber.id}-week-${lesson.week}`}
                                className={`board card ${cssClasses.SCHEDULE_BOARD} group-${group.id}`}
                            >
                                {itemInBoard(group, lesson, index)}
                            </Board>
                        </div>
                    ))}
                </section>
            ))}
        </section>
    );
};

export default Schedule;
