import * as actionTypes from '../actions/actionsType';

const teachersWish = (
    state = {
        wishes: [],
        wish: {}
    },
    action
) => {
    switch (action.type) {
        case actionTypes.ADD_WISH:
            return {
                ...state,
                wishes: [...state.wishes, action.result]
            };

        case actionTypes.DELETE_WISH:
            return {
                ...state,
                wishes: [
                    ...state.wishes.filter(
                        wishes => wishes.id !== action.result
                    )
                ]
            };

        case actionTypes.SHOW_ALL_WISH:
            return {
                ...state,
                wishes: [...action.result]
            };

        default:
            return state;
    }
};

export default teachersWish;
