import * as actionsType from './actionsType';

export const addWish = wish => {
    return {
        type: actionsType.ADD_WISH,
        result: wish
    };
};

export const deleteWish = id => {
    return {
        type: actionsType.DELETE_WISH,
        result: id
    };
};

export const showAllWishes = wishes => {
    return {
        type: actionsType.SHOW_ALL_WISH,
        result: wishes
    };
};
