import axios from '../helper/axios';
import { store } from '../index';
import { reset } from 'redux-form';
import { CLASSURL } from '../constants/axios';

import { CLASS_FORM } from '../constants/reduxForms';

import {
    addClassScheduleOne,
    setClassScheduleList,
    getClassScheduleOne,
    deleteClassScheduleOne,
    updateClassScheduleOne,
    clearClassScheduleOne
} from '../redux/actions/class';

import { handleSnackbarOpenService } from './snackbarService';
import { snackbarTypes } from '../constants/snackbarTypes';

import i18n from '../helper/i18n';

const resetFormHandler = formName => {
    store.dispatch(reset(formName));
};

export const getClassScheduleListService = dispatch => {
    axios
        .get(CLASSURL)
        .then(response => {
            let bufferArray = [];
            const results = response.data;
            for (const key in results) {
                bufferArray.push({
                    id: key,
                    ...results[key]
                });
            }
            store.dispatch(setClassScheduleList(bufferArray));
        })
        .catch(error => {
            handleSnackbarOpenService(
                true,
                snackbarTypes.ERROR,
                i18n.t(error.response.data.message, error.response.data.message)
            );
        });
};

export const addClassScheduleOneService = values => {
    if (values.id) {
        putAddClassScheduleOneService(values);
    } else {
        postAddClassScheduleOneService(values);
    }
};

export const putAddClassScheduleOneService = values => {
    axios
        .put(CLASSURL, values)
        .then(response => {
            store.dispatch(updateClassScheduleOne(response.data));
            resetFormHandler(CLASS_FORM);
            handleSnackbarOpenService(
                true,
                snackbarTypes.SUCCESS,
                i18n.t('serviceMessages:back.end.success.operation', {
                    cardType: i18n.t('formElements:class.label'),
                    actionType: i18n.t('serviceMessages:updated.label')
                })
            );
        })
        .catch(error => {
            handleSnackbarOpenService(
                true,
                snackbarTypes.ERROR,
                i18n.t(error.response.data.message, error.response.data.message)
            );
        });
};

export const postAddClassScheduleOneService = values => {
    axios
        .post(CLASSURL, values)
        .then(response => {
            store.dispatch(addClassScheduleOne(response.data));
            resetFormHandler(CLASS_FORM);
            handleSnackbarOpenService(
                true,
                snackbarTypes.SUCCESS,
                i18n.t('serviceMessages:back.end.success.operation', {
                    cardType: i18n.t('formElements:class.label'),
                    actionType: i18n.t('serviceMessages:created.label')
                })
            );
        })
        .catch(error => {
            handleSnackbarOpenService(
                true,
                snackbarTypes.ERROR,
                i18n.t(error.response.data.message, error.response.data.message)
            );
        });
};

export const getClassScheduleOneService = classId => {
    store.dispatch(getClassScheduleOne(classId));
};

export const deleteClassScheduleOneService = classId => {
    axios
        .delete(CLASSURL + `/${classId}`)
        .then(response => {
            store.dispatch(deleteClassScheduleOne(classId));
            handleSnackbarOpenService(
                true,
                snackbarTypes.SUCCESS,
                i18n.t('serviceMessages:back.end.success.operation', {
                    cardType: i18n.t('formElements:class.label'),
                    actionType: i18n.t('serviceMessages:deleted.label')
                })
            );
        })
        .catch(error => {
            handleSnackbarOpenService(
                true,
                snackbarTypes.ERROR,
                i18n.t(error.response.data.message, error.response.data.message)
            );
        });
};

export const clearClassScheduleOneService = () => {
    store.dispatch(clearClassScheduleOne());
    resetFormHandler(CLASS_FORM);
};
