import React from 'react';

import Card from '../../share/Card/Card';

import { FaEdit } from 'react-icons/fa';
import { MdDelete } from 'react-icons/md';

const LessonsList = props => {
    const lessons = props.lessons;

    const t = props.translation;

    const firstStringLetterCapitalHandle = str => {
        return str.replace(/^\w/, c => c.toUpperCase());
    };

    const everyFirstLetterCapitalHandle = str => {
        return str
            .toLowerCase()
            .split(' ')
            .map(s => s.charAt(0).toUpperCase() + s.substring(1))
            .join(' ');
    };

    return (
        <div>
            <section className="container-flex-wrap">
                {lessons.map(lesson => (
                    <Card class="done-card" key={lesson.id}>
                        <div className="cards-btns">
                            <FaEdit
                                title={t('delete.lesson')}
                                className="svg-btn"
                                onClick={() => props.onSelectLesson(lesson.id)}
                            />
                            <MdDelete
                                title={t('edit.lesson')}
                                className="svg-btn"
                                onClick={() => props.onClickOpen(lesson.id)}
                            />
                        </div>
                        <p>
                            {firstStringLetterCapitalHandle(
                                lesson.subjectForSite
                            )}
                            (<b>{lesson.lessonType}</b>)
                        </p>
                        <p>
                            {everyFirstLetterCapitalHandle(
                                lesson.teacherForSite
                            )}
                        </p>
                        <p>
                            {' '}
                            <b>{lesson.hours}</b>{' '}
                            {t('formElements:hours.label')}
                        </p>
                    </Card>
                ))}
            </section>
        </div>
    );
};

export default LessonsList;
