import React, { useEffect } from 'react';

import Card from '../../share/Card/Card';

import { Field, reduxForm } from 'redux-form';
import { connect } from 'react-redux';

import './LessonForm.scss';

import renderTextField from '../../share/renderedFields/input';
import renderSelectField from '../../share/renderedFields/select';

import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';

import { LESSON_FORM } from '../../constants/reduxForms';

import {
    required,
    lessThanZero,
    maxLengthValue
} from '../../validation/validateFields';
import { useTranslation } from 'react-i18next';
import { setUniqueErrorService } from '../../services/lessonService';
import { firstStringLetterCapital } from '../../helper/strings';

const useStyles = makeStyles(theme => ({
    openDialog: {
        '&': {
            textAlign: 'center',
            margin: 'auto'
        }
    }
}));

let LessonForm = props => {
    const { t } = useTranslation('formElements');

    const { handleSubmit, pristine, reset, submitting } = props;

    const classes = useStyles();

    const lesson = props.lesson;
    const lessonId = lesson.id;

    const isUniqueError = props.isUniqueError;

    const teachers = props.teachers;

    const subjects = props.subjects;

    const groupId = props.groupId;

    useEffect(() => {
        if (lessonId) {
            initializeFormHandler(lesson);
        } else {
            props.initialize();
        }
    }, [lessonId]);

    const initializeFormHandler = lesson => {
        props.initialize({
            lessonCardId: lesson.id,
            teacher: lesson.teacher.id,
            subject: lesson.subject.id,
            type: lesson.lessonType,
            hours: lesson.hours,
            teacherForSite: lesson.teacherForSite,
            subjectForSite: lesson.subjectForSite
        });
    };

    const setValueToTeacherForSiteHandler = teacherId => {
        const teacher = teachers.find(teacher => teacher.id === +teacherId);
        props.change('teacherForSite', concatTeacherNameHandler(teacher));
    };

    const setValueToSubjectForSiteHandler = subjectId => {
        const subject = subjects.find(subject => subject.id === +subjectId);
        if (!subject) {
            return props.change('subjectForSite', '');
        }
        const subjectName = subject.name;
        props.change('subjectForSite', subjectName);
    };

    const concatTeacherNameHandler = teacher => {
        if (!teacher) {
            return '';
        }
        return teacher.surname + ' ' + teacher.name + ' ' + teacher.patronymic;
    };

    return (
        <Card class="form-card">
            {groupId ? (
                <h2 className="form-title under-line">
                    {lessonId ? t('edit.title') : t('create.title')}
                    {t('lessony.label')}
                </h2>
            ) : (
                ''
            )}
            {groupId ? (
                <form onSubmit={handleSubmit}>
                    <Field
                        id="teacher"
                        name="teacher"
                        className="form-field"
                        component={renderSelectField}
                        label={t('teacher.label')}
                        {...(!isUniqueError ? {validate: [required]} : {error: isUniqueError})}
                        onChange={event => {
                            setValueToTeacherForSiteHandler(event.target.value);
                            setUniqueErrorService(false);
                        }}
                    >
                        <option value={''} />
                        {teachers.map(teacher => (
                            <option key={teacher.id} value={teacher.id}>
                                {concatTeacherNameHandler(teacher)}
                            </option>
                        ))}
                    </Field>
                    <Field
                        id="subject"
                        name="subject"
                        className="form-field"
                        component={renderSelectField}
                        label={t('subject.label')}
                        {...(!isUniqueError ? {validate: [required]} : {error: isUniqueError})}
                        onChange={event => {
                            setValueToSubjectForSiteHandler(event.target.value);
                            setUniqueErrorService(false);
                        }}
                    >
                        <option value={''} />
                        {subjects.map(subject => (
                            <option key={subject.id} value={subject.id}>
                                {subject.name}
                            </option>
                        ))}
                    </Field>
                    <Field
                        id="type"
                        name="type"
                        className="form-field"
                        component={renderSelectField}
                        label={t('type.label')}
                        {...(!isUniqueError ? {validate: [required]} : {error: isUniqueError})}
                        onChange={() => {
                            setUniqueErrorService(false);
                        }}
                    >
                        <option value={''} />
                        {props.lessonTypes.map((lessonType, index) => (
                            <option value={lessonType} key={index}>
                                {firstStringLetterCapital(
                                    lessonType.toLowerCase()
                                )}
                            </option>
                        ))}
                    </Field>
                    <Field
                        id="hours"
                        name="hours"
                        className="form-field"
                        type="number"
                        component={renderTextField}
                        label={t('hours.label')}
                        validate={[required, lessThanZero]}
                    />
                    <Field
                        id="teacherForSite"
                        name="teacherForSite"
                        className="form-field"
                        multiline
                        rowsMax="1"
                        margin="normal"
                        component={renderTextField}
                        label={t('teacher.label') + t('for.site.label')}
                        validate={[required, maxLengthValue]}
                    />
                    <Field
                        id="subjectForSite"
                        name="subjectForSite"
                        className="form-field"
                        multiline
                        rowsMax="1"
                        margin="normal"
                        component={renderTextField}
                        label={t('subject.label') + t('for.site.label')}
                        validate={[required, maxLengthValue]}
                    />
                    <div className="form-buttons-container">
                        <Button
                            className="buttons-style"
                            type="submit"
                            variant="contained"
                            color="primary"
                            disabled={pristine || submitting}
                        >
                            {t('save.button.label')}
                        </Button>
                        <Button
                            className="buttons-style"
                            type="button"
                            variant="contained"
                            disabled={pristine || submitting}
                            onClick={() => {
                                reset();
                                setUniqueErrorService(null);
                                props.onSetSelectedCard(null);
                            }}
                        >
                            {t('clear.button.label')}
                        </Button>
                    </div>
                </form>
            ) : (
                <div className={classes.openDialog}>
                    <h2>{t('group.label') + t('not.selected.label')}</h2>
                </div>
            )}
        </Card>
    );
};

const mapStateToProps = state => ({ lesson: state.lesson.lesson });

LessonForm = reduxForm({
    form: LESSON_FORM
})(LessonForm);

export default connect(mapStateToProps)(LessonForm);
