import { store } from '../index';
import { reset } from 'redux-form';
import { TEACHERURL } from '../constants/axios';
import { handleSnackbarOpenService } from './snackbarService';
import { snackbarTypes } from '../constants/snackbarTypes';
import { snackbarMessage } from '../constants/snackbarMessages';
import { actionType } from '../constants/actionTypes';
import { cardType } from '../constants/cardType';
import { TEACHER_FORM } from '../constants/reduxForms';

import axios from '../helper/axios';

import {
	showAllTeachers,
	deleteTeacher,
	selectTeacherCard,
	updateTeacherCard,
	addTeacher
} from '../redux/actions';

const resetForm = form => {
	store.dispatch(reset(form));
};

export const showAllTeachersService = () => {
	axios
		.get(TEACHERURL)
		.then(response => {
			store.dispatch(showAllTeachers(response.data));
		})
		.catch(error => {
			handleSnackbarOpenService(true, snackbarTypes.ERROR, 'Error');
		});
};

export const createTeacherService = values => {
	values.wish = [];
	axios
		.post(TEACHERURL, values)
		.then(response => {
			store.dispatch(addTeacher(response.data));
			resetForm(TEACHER_FORM);
			handleSnackbarOpenService(
				true,
				snackbarTypes.SUCCESS,
				snackbarMessage(cardType.TEACHER, actionType.CREATED)
			);
		})
		.catch(error => {
			handleSnackbarOpenService(true, snackbarTypes.ERROR, 'Error');
		});
};

const cardTeacher = teacher => {
	return {
		teacher: {
			id: teacher.id,
			name: teacher.name,
			surname: teacher.surname,
			patronymic: teacher.patronymic,
			position: teacher.position
		}
	};
};

export const updateTeacherService = data => {
	return axios
		.put(TEACHERURL, data.teacher)
		.then(response => {
			store.dispatch(updateTeacherCard(response.data));
			selectTeacherCardService(null);
			resetForm(TEACHER_FORM);
			handleSnackbarOpenService(
				true,
				snackbarTypes.SUCCESS,
				snackbarMessage(cardType.TEACHER, actionType.UPDATED)
			);
		})
		.catch(error => {
			handleSnackbarOpenService(true, snackbarTypes.ERROR, 'Error');
		});
};

export const handleTeacherService = values => {
	const teacher = cardTeacher(values);

	if (values.id) {
		updateTeacherService(teacher);
	} else {
		createTeacherService(values);
	}
};

export const removeTeacherCardService = id => {
	axios
		.delete(TEACHERURL + `/${id}`)
		.then(response => {
			store.dispatch(deleteTeacher(id));
			handleSnackbarOpenService(
				true,
				snackbarTypes.SUCCESS,
				snackbarMessage(cardType.TEACHER, actionType.DELETED)
			);
		})
		.catch(error => {
			handleSnackbarOpenService(true, snackbarTypes.ERROR, 'Error');
		});
};

export const selectTeacherCardService = teacherCardId => {
	store.dispatch(selectTeacherCard(teacherCardId));
};
