import { store } from '../index';
import { reset } from 'redux-form';
import { ROOMURL } from '../constants/axios';
import { ROOM_FORM } from '../constants/reduxForms';
import axios from '../helper/axios';

import {
    showListOfRooms,
    deleteRoom,
    addRoom,
    selectOneRoom,
    updateOneRoom,
    clearRoomOne
} from '../redux/actions/rooms';

import { handleSnackbarOpenService } from './snackbarService';
import { snackbarTypes } from '../constants/snackbarTypes';


import i18n from '../helper/i18n';

const resetFormHandler = form => {
    store.dispatch(reset(form));
};

export const showListOfRoomsService = () => {
    axios
        .get(ROOMURL)
        .then(res => {
            store.dispatch(showListOfRooms(res.data));
        })
        .catch(error => {
       
            // TO DO
            // NEED IT IN THE NEAREST FUTURE
            // handleSnackbarOpenService(
            //     true,
            //     snackbarTypes.ERROR,
            //     i18n.t(error.response.data.message, error.response.data.message)
            // );
        });
};

export const deleteRoomCardService = id => {
    axios
        .delete(ROOMURL + `/${id}`)
        .then(res => {
            store.dispatch(deleteRoom(id));
            handleSnackbarOpenService(
                true,
                snackbarTypes.SUCCESS,
                i18n.t('serviceMessages:back.end.success.operation', {
                    cardType: i18n.t('formElements:room.label'),
                    actionType: i18n.t('serviceMessages:deleted.label')
                })
                
            );
        })
        .catch(error => {
            handleSnackbarOpenService(
                true,
                snackbarTypes.ERROR,
                i18n.t(error.response.data.message, error.response.data.message)
            );
        });
};

const put = values => {
    axios
        .put(ROOMURL, values)
        .then(result => {
            store.dispatch(updateOneRoom(result.data));
            resetFormHandler(ROOM_FORM);
            handleSnackbarOpenService(
                true,
                snackbarTypes.SUCCESS,
                i18n.t('serviceMessages:back.end.success.operation', {
                    cardType: i18n.t('formElements:room.label'),
                    actionType: i18n.t('serviceMessages:updated.label')
                })
            );
        })
        .catch(error => {
            handleSnackbarOpenService(
                true,
                snackbarTypes.ERROR,
                i18n.t(error.response.data.message, error.response.data.message)
            );
        });
};
const post = values => {
    axios
        .post(ROOMURL, values)
        .then(res => {
            store.dispatch(addRoom(res.data));
            resetFormHandler(ROOM_FORM);
            handleSnackbarOpenService(
                true,
                snackbarTypes.SUCCESS,
                i18n.t('serviceMessages:back.end.success.operation', {
                    cardType: i18n.t('formElements:room.label'),
                    actionType: i18n.t('serviceMessages:created.label')
                })
            );
        })
        .catch(error => {
            handleSnackbarOpenService(
                true,
                snackbarTypes.ERROR,
                i18n.t(error.response.data.message, error.response.data.message)
            );
        });
};

export const createRoomService = values => {   
    if (values.id) {
        const newValue = {id: values.id, name:values.name, type:{id:+values.type}}
        put(newValue);
    } else {
        const newValue = {name:values.name, type:{id:+values.type}}
        post(newValue);
    }
};

export const selectOneRoomService = roomId => {
    store.dispatch(selectOneRoom(roomId));
};

export const clearRoomOneService = () => {
    store.dispatch(clearRoomOne());
    resetFormHandler(ROOM_FORM);
};
