import React, { useEffect } from 'react';
import { reduxForm, Field } from 'redux-form';
import { required, uniqWish } from '../../validation/validateFields';
import Button from '@material-ui/core/Button';
import renderTextField from '../../share/renderedFields/input';
import renderSelectField from '../../share/renderedFields/select';
import { useTranslation } from 'react-i18next';

const WishFrom = props => {
    const { t } = useTranslation('formElements');

    const {
        teacherWishId,
        handleSubmit,
        classScheduler,
        pristine,
        submitting,
        reset
    } = props;

    let class_on_day = [];

    if (classScheduler.length > 0) {
        classScheduler.map(classSchedulerOne => {
            class_on_day.push(classSchedulerOne.class_name);
        });
    }

    const days_of_week = [
        'Monday',
        'Tuesday',
        'Wedneday',
        'Thursday',
        'Friday',
        'Saturday',
        'Sunday'
    ];

    useEffect(() => {
        // let defWishes = {};

        // const teacterWishList = [];
        // if (teachers.length > 1) {
        //     for (let i = 0; i < class_on_day.length - 1; i++) {
        //         let one = teacterWishList.filter(
        //             wish => wish.class_id === class_on_day[i]
        //         );
        //         if (one[0] === undefined) {
        //             defWishes[`class_number${class_on_day[i]}`] = 'all_ok';
        //         } else {
        //             defWishes[`class_number${class_on_day[i]}`] = one[0];
        //         }
        //     }
        // } else {
        //     for (let i = 0; i < class_on_day.length - 1; i++) {
        //         defWishes[`class_number${class_on_day[i]}`] = 'all_ok';

        //         props.change(`class_number${class_on_day[i]}`, 'all_ok');
        //     }
        // }

        for (let i = 0; i <= class_on_day.length - 1; i++) {
            props.change(`class_number${class_on_day[i]}`, 'ok');
        }
    }, []);

    useEffect(() => {
        props.change('teacher_id', teacherWishId);
    }, [props.teacherWishId]);

    const initializeTeacherId = () => {
        props.initialize({ teacher_id: teacherWishId });

        for (let i = 0; i <= class_on_day.length - 1; i++) {
            props.change(`class_number${class_on_day[i]}`, 'ok');
        }
    };

    return (
        <form className="wish-form" onSubmit={handleSubmit}>
            <Field
                id="teacher_id"
                name="teacher_id"
                component={renderTextField}
                type="text"
            />

            <Field
                name="day_of_week"
                className="week-days"
                component={renderSelectField}
                label={t('teachery.wish.day')}
                type="text"
                validate={[required]}
            >
                <option />
                {days_of_week.map((day_of_week, index) => (
                    <option key={index} value={`${day_of_week}`}>
                        {day_of_week}
                    </option>
                ))}
            </Field>
            <Field
                name="evenOdd"
                className="evenOdd"
                component={renderSelectField}
                label="evenOdd"
                type="text"
                validate={[required]}
            >
                <option />
                <option value={'Even'}>Even</option>
                <option value={'Odd'}>Odd</option>
            </Field>

            {class_on_day.map(class_number => (
                <Field
                    key={class_number}
                    name={`class_number${class_number}`}
                    component={renderSelectField}
                    label={`${t(
                        'teachery.wish.classNumbaer'
                    )}: ${class_number}`}
                >
                    <option />
                    <option value={'ok'}>Ok</option>
                    <option value={'good'}>+</option>
                    <option value={'bad'}>-</option>
                </Field>
            ))}
            <div className="form-buttons-container wish-margin-top">
                <Button
                    className="buttons-style"
                    variant="contained"
                    color="primary"
                    type="submit"
                    disabled={pristine || submitting}
                >
                    {t('save.button.label')}
                </Button>
                <Button
                    className="buttons-style"
                    variant="contained"
                    type="button"
                    disabled={pristine || submitting}
                    onClick={() => {
                        reset();
                        initializeTeacherId();
                    }}
                >
                    {t('clear.button.label')}
                </Button>
            </div>
        </form>
    );
};

export default reduxForm({
    form: 'teacherWish'
})(WishFrom);
