import React, {useState, useEffect} from 'react';
import { connect } from 'react-redux';
import { Field, reduxForm } from 'redux-form';

import { ROOM_FORM_TYPE } from '../../constants/reduxForms';

import ConfirmDialog from '../../share/modals/dialog';
import { cardType } from '../../constants/cardType';
import Card from '../../share/Card/Card';
import { useTranslation } from 'react-i18next';
import renderTextField from '../../share/renderedFields/input';
import Button from '@material-ui/core/Button';
import { styled } from '@material-ui/core/styles';
import { MdDelete } from 'react-icons/md';
import { FaEdit } from 'react-icons/fa';
import { deleteTypeService, selectOneTypeService } from '../../services/roomTypesService';
import './AddNewRoomType.scss';

let NewRoomType = props => {
    const { handleSubmit, pristine, submitting, roomTypes } = props;
    const CreateBtn = styled(Button)({
        marginTop: '20px'
    });

    const [open, setOpen] = useState(false);
    const [typeId, setTypeId] = useState(-1);

 

    useEffect(() => {
        if (props.oneType.description) {
            props.initialize({
                description: props.oneType.description,
                id: props.oneType.id
            });
        } else {
            props.initialize({});
        }
    }, [props.oneType]);

    const { t } = useTranslation('formElements');

    const editHandler =  roomTypeId => {
        selectOneTypeService(roomTypeId);
    };

    const handleClickOpen = typeId => {
        setTypeId(typeId);
        setOpen(true);
    };

    const handleClose = typeId => {
        setOpen(false);
        if (!typeId) {
            return;
        }
        deleteTypeService(typeId);
    };
    return (
        <>
            <ConfirmDialog
                selectedValue={''}
                cardId={typeId}
                whatDelete={cardType.TYPE.toLowerCase()}
                open={open}
                onClose={handleClose}
            />
            <Card class="form-card room-form">
                <form className="new-type-container" onSubmit={handleSubmit}>
                    <Field
                        type="text"
                        name="description"
                        component={renderTextField}
                        placeholder={t("addType.label")}
                        label={t("newType.label")}
                        className="form-field"
                        variant="outlined"
                    />
                    <CreateBtn
                        color="primary"
                        className="btnStyle"
                        disabled={pristine || submitting}
                        variant="contained"
                        type="submit"
                    >
                        {t('save.button.label')}
                    </CreateBtn>
                </form>

                <ul className="new-types">
                    {roomTypes.map(roomType => (
                        <li
                            key={roomType.id}
                            value={roomType.description}
                            className="new-types-list"
                        >
                            <span>{roomType.description}</span>
                            <span className="buttons">
                                <FaEdit className="btn edit"  onClick={() => editHandler(roomType.id)}/>
                                <MdDelete
                                    className="btn delete"
                                    onClick={() => handleClickOpen(roomType.id)}
                                />
                                
                            </span>
                        </li>
                    ))}
                </ul>
            </Card>
        </>
    );
};

const mapStateToProps = state => ({
    oneType: state.roomTypes.oneType,
    roomTypes: state.roomTypes.roomTypes
});

NewRoomType = reduxForm({
    form: ROOM_FORM_TYPE
})(NewRoomType);

export default connect(mapStateToProps)(NewRoomType);
