import React from 'react';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import './Header.scss';

import { links } from '../../constants/links';

const Header = props => {
    const { t } = useTranslation('common');

    const roles = props.roles;

    let leftLinks = null;
    let rightLinks = null;
    if (props.userRole === roles.MANAGER) {
        leftLinks = (
            <Link to={links.SCHEDULE_PAGE} className="navLinks">
                {t('schedule.title')}
            </Link>
        );
        rightLinks = (
            <>
                <Link to={links.ADMIN_PAGE} className="navLinks">
                    {t('admin.title')}
                </Link>
                <Link to={links.LOGOUT} className="navLinks">
                    {t('logout.title')}
                </Link>
            </>
        );
    } else if (props.userRole === roles.USER) {
        rightLinks = (
            <Link to={links.LOGOUT} className="navLinks">
                {t('logout.title')}
            </Link>
        );
    } else {
        rightLinks = (
            <Link to={links.AUTH} className="navLinks">
                {t('login.title')}
            </Link>
        );
    }

    return (
        <>
            <header className="header">
                <nav className="header-blocks">
                    <Link to={links.HOME_PAGE} className="navLinks">
                        {t('home.title')}
                    </Link>
                    {leftLinks}
                </nav>
                <div className="header-blocks">{rightLinks}</div>
            </header>
        </>
    );
};

export default Header;
