const dayArray = [
    'MONDAY',
    'TUESDAY',
    'WEDNESDAY',
    'THURSDAY',
    'FRIDAY',
    'SATURDAY',
    'SUNDAY'
];

const daysPrep = [
    { day: 'MONDAY', class: [] },
    { day: 'TUESDAY', class: [] },
    { day: 'WEDNESDAY', class: [] },
    { day: 'THURSDAY', class: [] },
    { day: 'FRIDAY', class: [] },
    { day: 'SATURDAY', class: [] },
    { day: 'SUNDAY', class: [] }
];

const daysPrepFull = [
    { day: 'MONDAY', class: [] },
    { day: 'TUESDAY', class: [] },
    { day: 'WEDNESDAY', class: [] },
    { day: 'THURSDAY', class: [] },
    { day: 'FRIDAY', class: [] },
    { day: 'SATURDAY', class: [] },
    { day: 'SUNDAY', class: [] }
];

export const makeGroupSchedule = groupSchedule => {
    let evenArray = [];
    let oddArray = [];
    let group = {};

    const daysPrepArray = daysPrep;
    const evenDaysPrepArray = daysPrep;

    if (groupSchedule[0]) {
        group = groupSchedule[0].group;
        groupSchedule[0].days.map(day => {
            day.classes.map(classItem => {
                evenDaysPrepArray.map(dayPrep => {
                    if (dayPrep.day === day.day) {
                        dayPrep.class[classItem.class.id] = {
                            class: classItem.class,
                            card: classItem.weeks.even
                        };
                    }
                });

                daysPrepArray.map(dayPrep => {
                    if (dayPrep.day === day.day) {
                        dayPrep.class[classItem.class.id] = {
                            class: classItem.class,
                            card: classItem.weeks.odd
                        };
                    }
                });
            });
        });
    }
    daysPrepArray.map(day => {
        day.class.map((clas, clasIndex) => {
            if (!oddArray.hasOwnProperty(clasIndex)) {
                oddArray[clasIndex] = { class: clas.class, cards: {} };
            }

            dayArray.map(dayArr => {
                if (!oddArray[clasIndex].cards.hasOwnProperty(dayArr)) {
                    oddArray[clasIndex].cards[dayArr] = {};
                }
            });
            if (clas.card !== null && clas.card !== undefined) {
                if (clas.card.hasOwnProperty('teacherForSite')) {
                    oddArray[clasIndex].cards[day.day] = {
                        card: clas.card
                    };
                }
            }
        });
    });

    evenDaysPrepArray.map(day => {
        day.class.map((clas, clasIndex) => {
            if (!evenArray.hasOwnProperty(clasIndex)) {
                evenArray[clasIndex] = { class: clas.class, cards: {} };
            }

            dayArray.map(dayArr => {
                if (!evenArray[clasIndex].cards.hasOwnProperty(dayArr)) {
                    evenArray[clasIndex].cards[dayArr] = {};
                }
            });
            if (clas.card !== null && clas.card !== undefined) {
                if (clas.card.hasOwnProperty('teacherForSite')) {
                    evenArray[clasIndex].cards[day.day] = {
                        card: clas.card
                    };
                }
            }
        });
    });

    return {
        oddArray: oddArray,
        evenArray: evenArray,
        group: group
    };
};

export const makeFullSchedule = fullSchedule => {
    let maxClassesCount = 0;
    let groupsCount = fullSchedule.length;
    let groupList = [];
    fullSchedule.map(group => {
        groupList.push(group.group);
        group.days.map(day => {
            if (day.classes !== null && day.classes.length > maxClassesCount) {
                maxClassesCount = day.classes.length;
            }
        });
    });

    const daysPrepArrayFull = daysPrepFull;
    fullSchedule.map(group => {
        group.days.map(day => {
            day.classes.map(card => {
                const dayIndex = daysPrepArrayFull.findIndex(
                    x => x.day === day.day
                );
                if (dayIndex >= 0 && daysPrepArrayFull[dayIndex].class) {
                    let classIndex = daysPrepArrayFull[
                        dayIndex
                    ].class.findIndex(x => x.class.id === card.class.id);
                    if (classIndex < 0) {
                        daysPrepArrayFull[dayIndex].class.push({
                            class: card.class,
                            cards: { even: [], odd: [] }
                        });
                        classIndex = daysPrepArrayFull[
                            dayIndex
                        ].class.findIndex(x => x.class.id === card.class.id);
                    }
                    if (daysPrepArrayFull[dayIndex].class[classIndex].cards) {
                        if (
                            card.weeks.even !== null &&
                            card.weeks.odd !== null
                        ) {
                            card.weeks.even.weekly = 0;
                            card.weeks.even.skip = 0;
                            card.weeks.odd.weekly = 0;
                            card.weeks.odd.skip = 0;
                            if (
                                card.weeks.even.teacherForSite ===
                                card.weeks.odd.teacherForSite
                            ) {
                                card.weeks.even.skip = 1;
                                card.weeks.odd.weekly = 1;
                            }
                        }

                        const evenIndex = daysPrepArrayFull[dayIndex].class[
                            classIndex
                        ].cards.even.find(
                            item => item.group.id === group.group.id
                        );
                        if (!evenIndex) {
                            daysPrepArrayFull[dayIndex].class[
                                classIndex
                            ].cards.even.push({
                                group: group.group,
                                card: card.weeks.even
                            });
                        }

                        const oddIndex = daysPrepArrayFull[dayIndex].class[
                            classIndex
                        ].cards.odd.find(
                            item => item.group.id === group.group.id
                        );
                        if (!oddIndex) {
                            daysPrepArrayFull[dayIndex].class[
                                classIndex
                            ].cards.odd.push({
                                group: group.group,
                                card: card.weeks.odd
                            });
                        }
                    }
                }
            });
        });
    });

    daysPrepArrayFull.map(day => {
        day.class.map(clas => {
            if (
                clas.cards !== undefined &&
                clas.cards.even.length < groupList.length
            ) {
                clas.cards.even.push({
                    group: {},
                    card: null
                });
            }
            if (
                clas.cards !== undefined &&
                clas.cards.odd.length < groupList.length
            ) {
                clas.cards.odd.push({
                    group: {},
                    card: null
                });
            }
        });
    });
    return {
        maxClassesCount: maxClassesCount,
        groupsCount: groupsCount,
        groupList: groupList,
        resultArray: daysPrepArrayFull
    };
};
