import React, { useEffect, Fragment } from 'react';

import PropTypes from 'prop-types';
import Dialog from '@material-ui/core/Dialog';
import Card from '../../share/Card/Card';
import WishForm from '../../components/AddTeacherWishForm/WishForm';

import {
    allTeachersWishesService,
    createTeachersWishService,
    removeTeacherWishService
} from '../../services/teacherWishService';

import { getClassScheduleListService } from '../../services/classService';

import { MdDelete } from 'react-icons/md';

import './WishModal.scss';

const WishModal = props => {
    const {
        onCloseWish,
        teacherWishId,
        classScheduler,
        teacherWishes,
        openWish
    } = props;

    useEffect(() => allTeachersWishesService(), []);
    useEffect(() => getClassScheduleListService(), []);

    let class_names = [];

    if (classScheduler.length - 1 > 0) {
        classScheduler.map(classSchedulerOne => {
            class_names.push(classSchedulerOne.class_name);
        });
    }

    const teacherWishSubmit = values => {
        let wish = {
            id: Math.floor(Math.random() * 10001),
            teacher_id: values.teacher_id,
            teacherWishesList: [
                {
                    day_of_week: values.day_of_week,
                    evenOdd: values.evenOdd,
                    class_status: []
                }
            ]
        };

        for (let i = 0; i <= classScheduler.length - 1; i++) {
            let newWish = {
                class_id: class_names[i],
                status: values[`class_number${class_names[i]}`]
            };

            wish.teacherWishesList[0].class_status.push(newWish);
        }

        createTeachersWishService(wish);
    };

    const removeWish = id => {
        removeTeacherWishService(id);
    };

    const teacherWishOne = teacherWishes.filter(teacherWish => {
        return teacherWishId === +teacherWish.teacher_id;
    });

    const handleClose = () => {
        onCloseWish(teacherWishId);
    };

    return (
        <Dialog onClose={handleClose} open={openWish} maxWidth="lg">
            <h2 className="modal-teacher-title">
                Teacher wish for class schedule.
            </h2>

            <div className="cards-container">
                <Card class="wish-modal">
                    <WishForm
                        teacherWishId={teacherWishId}
                        classScheduler={classScheduler}
                        onSubmit={teacherWishSubmit}
                    />
                </Card>
                <section className="container-flex-wrap">
                    {teacherWishId !== undefined ? (
                        <div className="wish-card">
                            {teacherWishOne.length > 0 ? (
                                teacherWishOne.map((teacherWish, index) => (
                                    <Card class="day-week-status" key={index}>
                                        <div className="cards-btns">
                                            <MdDelete
                                                className="svg-btn delete-btn"
                                                onClick={() => {
                                                    removeWish(teacherWish.id);
                                                }}
                                            />
                                        </div>
                                        {teacherWish.teacherWishesList.map(
                                            (arrayWish, index) => (
                                                <Fragment key={index}>
                                                    <h3 className="wish-title-day">
                                                        {arrayWish.day_of_week}
                                                    </h3>
                                                    <p className="wish-title-week">
                                                        Week:
                                                        {arrayWish.evenOdd}
                                                    </p>
                                                    {arrayWish.class_status.map(
                                                        (
                                                            class_status,
                                                            index
                                                        ) => (
                                                            <div
                                                                className="class-status-block"
                                                                key={index}
                                                            >
                                                                <span>
                                                                    class{' '}
                                                                    {
                                                                        class_status.class_id
                                                                    }
                                                                </span>
                                                                :{' '}
                                                                <span>
                                                                    <span
                                                                        className={`_${class_status.status}`}
                                                                    >
                                                                        {
                                                                            class_status.status
                                                                        }
                                                                    </span>
                                                                </span>
                                                            </div>
                                                        )
                                                    )}
                                                </Fragment>
                                            )
                                        )}
                                    </Card>
                                ))
                            ) : (
                                <p className="teacher-without-wishes">
                                    This Teacher dont have any wish for schedule
                                </p>
                            )}
                        </div>
                    ) : (
                        <Card class="teacher-without-wishes">
                            <p>Some problem with Teacher id</p>
                        </Card>
                    )}
                </section>
            </div>
        </Dialog>
    );
};

WishModal.propTypes = {
    onCloseWish: PropTypes.func.isRequired,
    openWish: PropTypes.bool.isRequired,
    teacherWishId: PropTypes.number.isRequired
};

export default WishModal;
