import * as actionTypes from '../actions/actionsType';
import actions from 'redux-form/lib/actions';
import { actionType } from '../../constants/actionTypes';

const roomTypes = (
    state = {
        roomTypes: [],
        oneType: {}
    },
    action
) => {
    switch (action.type) {
        case actionTypes.CREATE_NEW_TYPE:
            return {
                ...state,
                roomTypes: [...state.roomTypes, action.result]
            };

        case actionTypes.GET_ALL_ROOM_TYPES:
            return {
                ...state,
                roomTypes: [...action.result]
            };
        case actionTypes.DELETE_TYPE:
            return {
                ...state,
                roomTypes: [
                    ...state.roomTypes.filter(
                        roomTypes => roomTypes.id !== action.result
                    )
                ]
            };
        case actionTypes.SELECT_ONE_TYPE:
            const one = state.roomTypes.filter(
                typeItem => typeItem.id === action.result
            );
            return {
                ...state,
                oneType: one[0]
            };
        case actionTypes.UPDATE_ONE_TYPE:
            let typeState = [...state.roomTypes];
            typeState[
            typeState.findIndex(typeItem => typeItem.id === action.result.id) 
            
            ] = action.result; 
         
            return {
                ...state,
                oneType: {},
                roomTypes: [...typeState]
            };

        default:
            return state;
    }
};

export default roomTypes;
