import React from 'react';

import { Field, reduxForm } from 'redux-form';

import renderTextField from '../../share/renderedFields/input';
import Card from '../../share/Card/Card';

import Button from '@material-ui/core/Button';
import CircularProgress from '@material-ui/core/CircularProgress';

import { REGISTRATION_FORM } from '../../constants/reduxForms';

import { email, password, required } from '../../validation/validateFields';
import { useTranslation } from 'react-i18next';

let RegistrationForm = props => {
    const { t } = useTranslation('formElements');
    const { handleSubmit } = props;

    const translation = props.translation;

    const error = props.registrationError;

    const errArr = error ? error.toLowerCase().split(' ') : '';
    const emailValidate = { validate: [required, email] };
    const emailErrorCondition = error && errArr.includes('email');
    const emailAdvancedValidate = {
        error: emailErrorCondition,
        helperText: emailErrorCondition ? error : ''
    };

    const passwordValidate = { validate: [required, password] };
    const passwordErrorCondition =
        error &&
        (errArr.includes('password') ||
            errArr.includes('passwords') ||
            errArr.includes(''));
    const passwordValidateAdvanced = {
        error: passwordErrorCondition,
        helperText: passwordErrorCondition ? error : ''
    };

    const retypePasswordValidate = { validate: [required, password] };
    const retypePasswordCondition =
        error && (errArr.includes('passwords') || errArr.includes(''));
    const retypePasswordValidateAdvanced = {
        error: retypePasswordCondition
    };

    let form = (
        <form onSubmit={handleSubmit}>
            <Field
                name="email"
                className="form-field"
                type="email"
                component={renderTextField}
                label={t('email.label')}
                {...(!error ? emailValidate : emailAdvancedValidate)}
                onChange={e => {
                    props.setError(null);
                }}
            />
            <Field
                name="password"
                className="form-field"
                type="password"
                component={renderTextField}
                label={t('password.label')}
                {...(!error ? passwordValidate : passwordValidateAdvanced)}
                onChange={e => {
                    props.setError(null);
                }}
            />
            <Field
                name="retypePassword"
                className="form-field"
                type="password"
                component={renderTextField}
                label={t('retype.password.label')}
                {...(!error
                    ? retypePasswordValidate
                    : retypePasswordValidateAdvanced)}
            />
            <Button
                className="buttons-style"
                type="submit"
                variant="contained"
                color="primary"
            >
                {translation('create.account')}
            </Button>
            <button
                type="button"
                className="auth-link"
                onClick={() => {
                    props.switchAuthMode(true);
                    props.setError(null);
                }}
            >
                {translation('account.exist')}
            </button>
        </form>
    );

    if (props.isLoading) {
        form = <CircularProgress />;
    }
    return (
        <section className="auth-form">
            <Card class="auth-card">
                <h2 className="under-line">
                    {props.translation('registration.page.title')}
                </h2>
                {form}
            </Card>
        </section>
    );
};

RegistrationForm = reduxForm({
    form: REGISTRATION_FORM
})(RegistrationForm);

export default RegistrationForm;
