import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux';
import { useTranslation } from 'react-i18next';

import Button from '@material-ui/core/Button';
import './GroupSchedulePage.scss';

import {
    makeGroupSchedule,
    makeFullSchedule
} from '../../helper/prepareSchedule';
import {
    renderGroupTable,
    renderFullSchedule
} from '../../helper/renderScheduleTable';
import {
    getGroupSchedule,
    getFullSchedule,
    setScheduleTypeService
} from '../../services/scheduleService';

const useStyles = makeStyles(theme => ({
    root: {
        '& > *': {
            margin: theme.spacing(1)
        }
    }
}));

const GroupSchedulePage = props => {
    const classes = useStyles();
    let { groupSchedule, fullSchedule } = props;

    const matchFunction = scheduleType => {
        switch (scheduleType) {
            case 'full':
                getFullSchedule();
                break;
            case 'group':
                getGroupSchedule();
                break;
            default:
                break;
        }
        return 1;
    };
    useEffect(() => {
        matchFunction(props.scheduleType);
    }, [props.scheduleType]);

    const { t } = useTranslation('common');

    switch (props.scheduleType) {
        case 'group':
            const resultArrays = makeGroupSchedule(groupSchedule);
            return (
                <section className={classes.root}>
                    <p>{t('common:greetings.schedule.message')}</p>
                    <Button
                        className="buttons-style"
                        variant="contained"
                        color="primary"
                        onClick={() => {
                            setScheduleTypeService('full');
                        }}
                    >
                        {t('common:full.schedule.label')}
                    </Button>
                    <Button
                        className="buttons-style"
                        variant="contained"
                        color="primary"
                        onClick={() => {
                            setScheduleTypeService('group');
                        }}
                    >
                        {t('common:group.schedule.label')}
                    </Button>
                    <br />
                    <p>{resultArrays.group.title}</p>
                    <p>{t('common:odd.week')}</p>
                    {renderGroupTable(resultArrays.oddArray, 1)}
                    <p>{t('common:even.week')}</p>
                    {renderGroupTable(resultArrays.evenArray, 0)}
                </section>
            );

        case 'full':
            const result = makeFullSchedule(fullSchedule);
            return (
                <section className={classes.root}>
                    <p>{t('common:greetings.schedule.message')}</p>
                    <Button
                        className="buttons-style"
                        variant="contained"
                        color="primary"
                        onClick={() => {
                            setScheduleTypeService('full');
                        }}
                    >
                        {t('common:full.schedule.label')}
                    </Button>
                    <Button
                        className="buttons-style"
                        variant="contained"
                        color="primary"
                        onClick={() => {
                            setScheduleTypeService('group');
                        }}
                    >
                        {t('common:group.schedule.label')}
                    </Button>
                    {renderFullSchedule(result)}
                </section>
            );
        default:
            setScheduleTypeService('full');
            return (
                <section className={classes.root}>
                    <p>{t('common:greetings.schedule.message')}</p>
                    <Button
                        className="buttons-style"
                        variant="contained"
                        color="primary"
                        onClick={() => {
                            setScheduleTypeService('full');
                        }}
                    >
                        {t('common:full.schedule.label')}
                    </Button>
                    <Button
                        className="buttons-style"
                        variant="contained"
                        color="primary"
                        onClick={() => {
                            setScheduleTypeService('group');
                        }}
                    >
                        {t('common:group.schedule.label')}
                    </Button>
                </section>
            );
    }
};
const mapStateToProps = state => ({
    scheduleType: state.schedule.scheduleType,
    groupSchedule: state.schedule.groupSchedule,
    fullSchedule: state.schedule.fullSchedule
});
export default connect(mapStateToProps)(GroupSchedulePage);
