import React from 'react';
import Card from '../../share/Card/Card';

import { MdDelete } from 'react-icons/md';

const ScheduleItem = props => {
    let lesson = props.lesson;
    let itemId, classNumber;

    const item = props.item || null;
    const t = props.translation;
    const { fStrLetterCapital, eFLetCapital } = props;

    if (item) {
        lesson = item.lesson;
        itemId = item.id;
        classNumber = item.classNumber;
    }

    const index = props.index;

    const itemNodeId = `card-${lesson.id}-group-${lesson.group.id}-${index}`;
    const deleteNodeId = `delete-${lesson.id}-${lesson.group.id}-${index}`;
    const itemNode = document.getElementById(itemNodeId);
    const deleteNode = document.getElementById(deleteNodeId);

    if (itemNode && deleteNode) {
        itemNode.addEventListener('mouseenter', () => {
            deleteNode.style.display = 'block';
        });
        itemNode.addEventListener('mouseleave', () => {
            deleteNode.style.display = 'none';
        });
    }

    return (
        <Card
            id={itemNodeId}
            class={props.class}
            draggable={true}
            classes={props.classScheduler}
        >
            <input
                type="hidden"
                value={JSON.stringify({
                    lesson: lesson,
                    id: itemId,
                    classNumber
                })}
            />
            {props.inBoard ? (
                <>
                    <div className="cards-btns delete-item" id={deleteNodeId}>
                        <MdDelete
                            title={t('common:delete.schedule.item')}
                            className="svg-btn"
                            onClick={() =>
                                props.deleteItem(
                                    itemId,
                                    item.lesson.group.id
                                )
                            }
                        />
                    </div>
                    <p>
                        {t('common:room.card.title')} <b>{item.room.name}</b>
                    </p>
                </>
            ) : (
                ''
            )}
            <p>
                {fStrLetterCapital(lesson.subjectForSite)}(
                <b>{lesson.lessonType}</b>)
            </p>
            <p>{eFLetCapital(lesson.teacherForSite)}</p>
            <p>
                <b>1</b> {t('formElements:hours.label')}
            </p>
        </Card>
    );
};

export default ScheduleItem;
