import { store } from '../index';

import teachersWish from '../containers/TeachersList/teacherWishes.json';

import { showAllWishes, deleteWish, addWish } from '../redux/actions';

export const allTeachersWishesService = () => {
    store.dispatch(showAllWishes(teachersWish));
};

export const createTeachersWishService = values => {
    store.dispatch(addWish(values));
};

export const removeTeacherWishService = id => {
    store.dispatch(deleteWish(id));
};
