import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import LessonPage from '../LessonPage/LessonPage';
import TeacherList from '../TeachersList/TeachersList';
import ClassSchedule from '../ClassSchedule/ClassSchedule';
import GroupList from '../GroupList/GroupList';
import RoomList from '../RoomList/RoomList';
import SubjectPage from "../SubjectPage/SubjectPage";

import { connect } from 'react-redux';
import { useTranslation } from 'react-i18next';


function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <Typography
            component="div"
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && <Box p={3}>{children}</Box>}
        </Typography>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired
};

function a11yProps(index) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`
    };
}

const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
        backgroundColor: theme.palette.background.paper
    }
}));

const SimpleTabs = props => {
    const { t } = useTranslation('common');
    const classes = useStyles();
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };


    return (
        <div className={classes.root}>
            <AppBar position="static">
                <Tabs
                    value={value}
                    onChange={handleChange}
                    aria-label="simple tabs example"
                >
                    <Tab
                        label={t('lesson.management.title')}
                        {...a11yProps(0)}
                    />
                    <Tab
                        label={t('teacher.management.title')}
                        {...a11yProps(1)}
                    />
                    <Tab
                        label={t('group.management.title')}
                        {...a11yProps(2)}
                    />
                    <Tab
                        label={t('class.management.title')}
                        {...a11yProps(3)}
                    />
                    <Tab label={t('room.management.title')}
                         {...a11yProps(4)} />
                    <Tab
                        label={t('subject.management.title')}
                        {...a11yProps(5)}
                    />
                </Tabs>
            </AppBar>

            <TabPanel value={value} index={0}>
                <LessonPage />
            </TabPanel>

            <TabPanel value={value} index={1}>
                <TeacherList />
            </TabPanel>

            <TabPanel value={value} index={2}>
                <GroupList />
            </TabPanel>
            <TabPanel value={value} index={3}>
                <ClassSchedule />
            </TabPanel>
            <TabPanel value={value} index={4}>
                <RoomList />
            </TabPanel>
            <TabPanel value={value} index={5}>
                <SubjectPage />
            </TabPanel>
        </div>
    );
};

const mapStateToProps = state => ({
    classScheduler: state.classActions.classScheduler,
    ClassScheduleOne: state.classActions.classScheduleOne
});

export default connect(mapStateToProps, {})(SimpleTabs);
