import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';

import { resetFormService } from '../../services/lessonService';
import { handleSnackbarOpenService } from '../../services/snackbarService';

import { LOGIN_FORM, REGISTRATION_FORM } from '../../constants/reduxForms';
import { snackbarTypes } from '../../constants/snackbarTypes';
import { links } from '../../constants/links';

import './Auth.scss';

import LoginForm from '../../components/LoginForm/LoginForm';
import RegistrationForm from '../../components/RegistrationForm/RegistrationForm';

import {
    authUser,
    registerUser,
    setAuthError,
    setLoading
} from '../../redux/actions/index';

import { useTranslation } from 'react-i18next';
import { userRoles } from '../../constants/userRoles';

const Auth = props => {
    const { t } = useTranslation('common');
    const [isLogin, setLogin] = useState(true);

    const error = props.error;

    if (isLogin) {
        document.title = t('login.page.title');
    } else {
        document.title = t('registration.page.title');
    }

    const loginHandler = loginData => {
        if (!loginData.email || !loginData.password) {
            props.setError(t('empty.fields'));
            return;
        }
        props.onAuth(loginData);
        props.setLoading(true);
        resetFormService(LOGIN_FORM);
    };

    const registrationHandler = registrationData => {
        if (registrationData.password !== registrationData.retypePassword) {
            props.setError(t('different.passwords'));
            return;
        }
        props.onRegister({
            email: registrationData.email,
            password: registrationData.password
        });
        props.setLoading(true);
        resetFormService(REGISTRATION_FORM);
    };

    let isSuccess, message;
    const isLoading = props.loading;

    if (!error && props.userRole) {
        const token = props.token;
        isSuccess = !!token;
        message = t('successful.login.message');
        handleSnackbarOpenService(true, snackbarTypes.SUCCESS, message);
    }

    const commonCondition = !error && isSuccess && !isLoading;

    let authRedirect = null;
    if (commonCondition && props.userRole === userRoles.MANAGER) {
        authRedirect = <Redirect to={links.ADMIN_PAGE} />;
    } else if (commonCondition && props.userRole === userRoles.USER) {
        authRedirect = <Redirect to={links.HOME_PAGE} />;
    }

    const switchAuthModeHandler = isLogin => {
        setLogin(isLogin);
    };

    useEffect(() => {
        if (
            !isLogin &&
            props.response &&
            props.response.data.hasOwnProperty('message')
        ) {
            setLogin(true);
            message = t('successful.registered.message');
            handleSnackbarOpenService(true, snackbarTypes.SUCCESS, message);
        }
    }, [props.response]);

    return (
        <div className="auth-form">
            {authRedirect}
            {isLogin ? (
                <LoginForm
                    isLoading={isLoading}
                    loginError={error}
                    onSubmit={loginHandler}
                    switchAuthMode={switchAuthModeHandler}
                    translation={t}
                    setError={props.setError}
                />
            ) : (
                <RegistrationForm
                    isLoading={isLoading}
                    registrationError={error}
                    onSubmit={registrationHandler}
                    switchAuthMode={switchAuthModeHandler}
                    translation={t}
                    setError={props.setError}
                />
            )}
        </div>
    );
};

const mapStateToProps = state => ({
    response: state.auth.response,
    error: state.auth.error,
    token: state.auth.token,
    userRole: state.auth.role,
    loading: state.loadingIndicator.loading
});

const mapDispatchToProps = dispatch => {
    return {
        onAuth: data => dispatch(authUser(data)),
        onRegister: data => dispatch(registerUser(data)),
        setLoading: isLoading => dispatch(setLoading(isLoading)),
        setError: error => dispatch(setAuthError(error))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Auth);
