import { store } from '../index';
import { reset } from 'redux-form';
import { ROOMURL } from '../constants/axios';
import { ROOM_FORM_TYPE } from '../constants/reduxForms';
import axios from '../helper/axios';

import {
    createNewType,
    getAllRoomTypes,
    selectOneType,
    updateOneType,
    deleteType
} from '../redux/actions/roomTypes';

import { handleSnackbarOpenService } from './snackbarService';
import { snackbarTypes } from '../constants/snackbarTypes';


import i18n from '../helper/i18n';

const resetForm = form => {
    store.dispatch(reset(form));
};

export const createNewTypeService = values => {
    if (values.id) {
        updateOneTypeService(values);
        return;
    }
    store.dispatch(createNewType(values));
    resetForm(ROOM_FORM_TYPE);
    handleSnackbarOpenService(
        true,
        snackbarTypes.SUCCESS,
        i18n.t('serviceMessages:back.end.success.operation', {
            cardType: i18n.t('formElements:type.label'),
            actionType: i18n.t('serviceMessages:created.label')
        })
    );
};

export const getAllRoomTypesService = () => {
    axios
        .get('room-types')
        .then(res => {
            store.dispatch(getAllRoomTypes(res.data));
        })
        .catch(error => {
            handleSnackbarOpenService(
                true,
                snackbarTypes.ERROR,
                i18n.t(error.response.data.message, error.response.data.message)
            );
        });
};

export const deleteTypeService = values => {
    store.dispatch(deleteType(values));
    handleSnackbarOpenService(
        true,
        snackbarTypes.SUCCESS,
        i18n.t('serviceMessages:back.end.success.operation', {
            cardType: i18n.t('formElements:type.label'),
            actionType: i18n.t('serviceMessages:deleted.label')
        })
    );
};

export const selectOneTypeService = value => {
    store.dispatch(selectOneType(value));
};

export const updateOneTypeService = values => {
    store.dispatch(updateOneType(values));
    resetForm(ROOM_FORM_TYPE);
    handleSnackbarOpenService(
        true,
        snackbarTypes.SUCCESS,
        i18n.t('serviceMessages:back.end.success.operation', {
            cardType: i18n.t('formElements:class.label'),
            actionType: i18n.t('serviceMessages:updated.label')
        })
    );
};
