import React, {useEffect, useState} from "react";
import { connect } from "react-redux";
import SnackbarComponent from '../../share/Snackbar/SnackbarComponent';
import ConfirmDialog from '../../share/modals/dialog';

import AddSubject from "../../components/AddSubjectForm/AddSubjectForm";
import {
    showAllSubjectsService,
    removeSubjectCardService,
    handleSubjectService,
    selectSubjectService,
    clearSubjectService
} from '../../services/subjectService'

import { handleSnackbarCloseService } from '../../services/snackbarService';
import {search} from "../../helper/search";
import './SubjectPage.scss';
import SearchPanel from "../../share/SearchPanel/SearchPanel";
import { MdDelete } from "react-icons/md";
import { FaEdit } from 'react-icons/fa';
import Card from "../../share/Card/Card";
import {useTranslation} from "react-i18next";
import NotFound from "../../share/NotFound/NotFound";

const SubjectPage = (props) => {
    const { t } = useTranslation('formElements');
    const [open, setOpen] = useState(false);
    const [subjectId, setSubjectId] = useState(-1);
    const [term, setTerm] = useState('');

    useEffect(() => showAllSubjectsService(), []);

    const { isSnackbarOpen, snackbarType, snackbarMessage } = props;

    const submit = values => {
        handleSubjectService(values);
    };

    const handleEdit = subjectId => {
        selectSubjectService(subjectId)
    };

    const handleClickOpen = (subjectId) => {
        setSubjectId(subjectId);
        setOpen(true);
    };

    const handleClose = (subjectId) => {
        setOpen(false);
        if (!subjectId) {
            return;
        }
        removeSubjectCardService(subjectId);
    };

    const handleSnackbarClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }

        handleSnackbarCloseService();
    };

    const handleFormReset = () => {
        clearSubjectService()
    };

    const visibleItems = search(props.subjects, term, ['name']);

    const SearchChange = (term) => {
        setTerm(term);
    };

    return (
        <>
            <ConfirmDialog selectedValue={''} cardId={subjectId} whatDelete={'subject'} open={open} onClose={handleClose}/>
            <div className="cards-container">
                <aside className="search-list__panel">
                <SearchPanel SearchChange={SearchChange}/>
            
                <AddSubject className='form' onSubmit={submit} onReset={handleFormReset}/>
                </aside>
                <section className='container-flex-wrap wrapper'>
                {visibleItems.length === 0 && <NotFound name={t('subjecty.label')}/>}
                    {visibleItems.map(subject => (
                        <Card key={subject.id} class='subject-card done-card'>

                            <div className="cards-btns">
                                <FaEdit className="svg-btn edit-btn" title={t('edit.title')}
                                        onClick={() => handleEdit(subject.id)}/>
                                <MdDelete className="svg-btn delete-btn" title={t('delete.title')}
                                          onClick={() => handleClickOpen(subject.id)}/>
                            </div>
                            <p className="subject-card__description">{t('subject.label')+':'} </p>
                            <h2 className="subject-card__name">{subject.name}</h2>
                        </Card>))}
                </section>
            </div>
            <SnackbarComponent
                message={snackbarMessage}
                type={snackbarType}
                isOpen={isSnackbarOpen}
                handleSnackbarClose={handleSnackbarClose}
            />
        </>
    );
};
const mapStateToProps = state => ({
    subjects: state.subjects.subjects,
    isSnackbarOpen: state.snackbar.isSnackbarOpen,
    snackbarType: state.snackbar.snackbarType,
    snackbarMessage: state.snackbar.message
});

export default connect(mapStateToProps, {})(SubjectPage);
