import * as moment from 'moment';
import {
    checkUniqClassName,
    checkUniqueGroup,
    checkUniqueRoomName,
    checkUniqueSubject,
    checkUniqWish,
    timeIntersectService
} from './storeValidation';
import i18n from '../helper/i18n';

export const required = value =>
    value ? undefined : i18n.t('validationMessages:required.message');

export const lessThanZero = value =>
    value > 0
        ? undefined
        : i18n.t('validationMessages:bigger.than.zero.message');

const minLength = min => value =>
    value && value.length < min
        ? i18n.t('validationMessages:bigger.than.char.message', { min: min })
        : undefined;

export const minLengthValue = minLength(3);

const maxLength = max => value =>
    value && value.length > max
        ? i18n.t('validationMessages:less.than.char.message', {
              max: max
          })
        : undefined;

export const maxLengthValue = maxLength(55);

export const lessThanTime = (value, previousValue, allValues) => {
    const otherField = 'endTime';
    if (allValues.values[otherField] === undefined) return undefined;
    return moment(value, 'HH:mm').toDate() <=
        moment(allValues.values[otherField], 'HH:mm').toDate() &&
        allValues.values[otherField] !== undefined
        ? undefined
        : i18n.t('validationMessages:less.than.field.message', {
              field: i18n.t('formElements:class.to.label')
          });
};

export const greaterThanTime = (value, previousValue, allValues) => {
    const otherField = 'startTime';
    if (allValues.values[otherField] === undefined) return undefined;
    return moment(value, 'HH:mm').toDate() >=
        moment(allValues.values[otherField], 'HH:mm').toDate()
        ? undefined
        : i18n.t('validationMessages:bigger.than.field.message', {
              field: i18n.t('formElements:class.from.label')
          });
};

export const uniqueClassName = value => {
    return checkUniqClassName(value);
};

export const uniqueGroup = value => {
    return checkUniqueGroup(value);
};

export const uniqueSubject = value => {
    return checkUniqueSubject(value);
};

export const timeIntersect = (value, previousValue, allValues) => {
    return timeIntersectService(
        allValues.values['startTime'],
        allValues.values['endTime']
    );
};

export const uniqueRoomName = value => {
    return checkUniqueRoomName(value);
};

export const uniqWish = value => {
    return checkUniqWish(value);
};

export const email = value =>
    value && !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value)
        ? i18n.t('validationMessages:email')
        : undefined;

export const password = value =>
    value && !/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[^a-zA-Z0-9])(?=.*[!@#$%^&*]).{8,}$/.test(value)
        ? i18n.t('validationMessages:password')
        : undefined;

